body {
	font-family: "Heebo", sans-serif;
	font-size: 14px;
	line-height: 1.5em;
	letter-spacing: -0.007em;
	color: #333;
	background: #FFF;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	width: 100%;
	height: 100%;
	overflow-x: hidden;
	-ms-user-select: none;
	user-select: none;
	-moz-user-select: none;
	-webkit-user-select: none
}

a {
	-webkit-transition: .2s all;
	transition: .2s all;
}

a:hover {
	color: #565EFF;
	text-decoration: none
}

a:active,
a:focus {
	text-decoration: none
}

textarea:focus,
input:focus {
	outline: none;
}

.loading {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 15000;
	background: #FFF;
	display: -webkit-box;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: center;
	justify-content: center
}

.loading .spinner-grow {
	width: 100px;
	height: 100px
}

.appHeader1 {
	padding: 0 24px;
	height: 55px;
	background-color: #009688;
}

.appHeader1 .pageTitle {
	line-height: 3em;
	font-size: 20px;
	font-weight: 500;
	letter-spacing: .02em;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	color: #fff;
}

.appHeader {
	height: 55px;
	display: -webkit-box;
	display: flex;
	-webkit-box-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	align-items: center;
	/*position:fixed;*/
	top: 0;
	left: 0;
	right: 0;
	z-index: 9990;
	background: #fff;
	border-bottom: 1px solid #E5E9F2;
	color: #1B2C42
}

.appHeader .pageTitle {
	color: blue;
	font-size: 28px;
	text-align: center;
	line-height: 1em;
}

.appHeader .pageTitle .text-muted {
	font-size: 11px;
	margin: 0;
	line-height: 1.2em
}

.appHeader .pageTitle .image {
	height: 20px
}

.appHeader1 .left,
.appHeader1 .right {
	position: absolute;
	top: 0;
	-webkit-transition: 0.2s all;
	transition: 0.2s all;
	height: 56px;
	min-width: 56px;
	display: -webkit-box;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: center;
	justify-content: center
}

.appHeader .left .btn,
.appHeader .right .btn {
	margin-right: 10px
}

.appHeader1 .left .icon,
.appHeader1 .right .icon {
	width: 56px;
	height: 56px;
	color: #fff;
	display: -webkit-box;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: center;
	justify-content: center
}

.appHeader1 .left .icon i,
.appHeader1 .right .icon i {
	font-size: 26px
}

.appHeader1 .left:active,
.appHeader1 .right:active {
	opacity: 1
}

.appHeader1 .left {
	left: 0;
	right: auto
}

.appHeader1 .right {
	right: 0;
	left: auto
}

.appHeader1 .right .link {
	margin-right: 20px;
	color: #1B2C42;
	font-weight: 500
}

.appHeader.color-light {
	border-bottom: 0
}

.appHeader.color-light .pageTitle {
	color: #fff
}

.appHeader.color-light .left .icon,
.appHeader.color-light .right .icon {
	color: #fff
}

.searchBox {
	position: fixed;
	display: none;
	left: 0;
	top: 0;
	z-index: 10500;
	right: 0;
	height: 56px;
	background: #fff;
	box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 2px 1px -1px rgba(0, 0, 0, 0.12), 0 1px 3px 0 rgba(0, 0, 0, 0.2)
}

.searchBox .inputIcon {
	font-size: 26px;
	height: 56px;
	width: 56px;
	display: -webkit-box;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: center;
	justify-content: center;
	position: absolute;
	left: 0;
	top: 0;
	color: #1B2C42
}

.searchBox .form-control {
	height: 56px;
	background: transparent;
	border-radius: 0;
	padding-left: 56px;
	padding-right: 56px
}

.searchBox .form-control:focus {
	background: transparent
}

.searchBox .closeButton {
	position: absolute;
	right: 0;
	top: 0;
	width: 56px;
	height: 56px;
	text-shadow: none;
	font-size: 26px;
	display: -webkit-box;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: center;
	justify-content: center;
	color: #8D95A0
}

.searchBox .closeButton:hover,
.searchBox .closeButton:active {
	opacity: .7
}

.searchBlock {
	position: relative
}

.searchBlock .inputIcon {
	position: absolute;
	z-index: 1;
	font-size: 26px;
	width: 56px;
	height: 50px;
	display: -webkit-box;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: center;
	justify-content: center
}

.searchBlock .form-control {
	padding-left: 56px
