.appBottomMenu {
	min-height: 56px;
	position: fixed;
	z-index: 9999;
	bottom: 0;
	left: 0;
	right: 0;
	background: #FFF;
	display: -webkit-box;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: center;
	justify-content: center;
	padding-bottom: env(safe-area-inset-bottom);
	-webkit-box-shadow: 0 3px 14px 2px rgba(0, 0, 0, .12);
	box-shadow: 0 3px 14px 2px rgba(0, 0, 0, .12);
