body.sidebarActive {
	overflow: hidden
}

.sidebarWrapper {
	width: 100%;
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 11000;
	overflow: auto;
	display: none;
	background: rgba(0, 0, 0, 0.5)
}

.sidebarWrapper .overlay {
	position: fixed;
	left: 250px;
	right: 0;
	top: 0;
	bottom: 0
}

.sidebar {
	background: #fff;
	box-shadow: 2px 2px 5px 0 rgba(0, 0, 0, 0.15);
	width: 250px;
	position: absolute;
	left: 0;
	top: 0;
	bottom: 0;
	z-index: 3;
	overflow: auto;
	left: -90%;
	-webkit-transition: 0.2s all;
	transition: 0.2s all
}

.sidebar .profilebox {
	padding: 20px
}

.sidebar .profilebox .avatar {
	width: 48px;
	height: 48px;
	border-radius: 100px;
	margin-bottom: 12px
}

.sidebar .profilebox .title {
	font-size: 18px;
	font-weight: 700;
	letter-spacing: 0;
	color: #1B2C42;
	margin: 0 0 6px 0
}

.sidebar .profilebox .lead {
	font-size: 14px;
	color: #8D95A0;
	font-weight: 400;
	margin: 0
}

.sidebar .profilebox .button {
	position: absolute;
	right: 15px;
	top: 20px;
	font-size: 22px
}

.sidebar .profilebox .button a {
	display: inline-block;
	padding: 4px 5px;
	margin-left: 5px;
	color: #1B2C42
}

.sidebar .bottomButton {
	position: absolute;
	left: 0
}

.sidebarMenu {
	padding: 0;
	margin: 0 0 20px 0
}

.sidebarMenu>li {
	display: block
}

.sidebarMenu>li>a {
	display: block;
	color: #1B2C42;
	padding: 12px 40px 12px 60px;
	position: relative
}

.sidebarMenu>li>a .badge {
	position: absolute;
	right: 20px;
	top: 15px;
	padding: 2px 6px
}

.sidebarMenu>li>a i.icon {
	text-align: center;
	font-size: 26px;
	position: absolute;
	left: 20px;
	top: 10px
}

.sidebarMenu>li>a .avatar {
	width: 26px;
	height: 26px;
	position: absolute;
	left: 20px;
	top: 11px;
	border-radius: 100px
}

.sidebarMenu>li>a:active {
	background: rgba(229, 233, 242, 0.4)
}

.sidebarMenu li.title {
	padding-left: 20px;
	font-size: 14px;
	color: #8D95A0;
	font-weight: 500;
	padding-bottom: 10px
}

.sidebar.is-active {
	left: 0
}

