	display: -webkit-inline-box;
	display: inline-flex
}

.custom-switch .custom-control-label {
	padding-left: 4px
}

.custom-switch .custom-control-label:before {
	height: 32px;
	width: 64px;
	background: #FFF;
	border-radius: 100px !important;
